import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { SaleComponent } from './sale.component';
import { SaleInvoiceListComponent } from '../sale-invoice-list/sale-invoice-list.component';
import { EstimateListComponent } from '../estimate-list/estimate-list.component';
import { PaymentInListComponent } from '../../payment-in-list/payment-in-list.component';
import { SalesReturnListComponent } from '../sales-return-list/sales-return-list.component';
import { SaleorderListComponent } from '../../saleorder-list/saleorder-list.component';
import { SundryDebtorComponent } from '../sundry-debtor/sundry-debtor.component';

const routes: Routes = [
  {
    path:'',
    component:SaleComponent,
    children: [
      {
        path:'',
        redirectTo: 'sale_invoice_list',
        pathMatch: 'full'
      },
      {
        path:'sale_invoice_list',
        component:SaleInvoiceListComponent,
      },
      {
        path:'payment_in_list',
        component:PaymentInListComponent,
      },
      {
        path:'estimation_list',
        component:EstimateListComponent,
      },
      {
        path:'sale_order_list',
        component:SaleorderListComponent,
      },
      {
        path:'sale_return_list',
        component:SalesReturnListComponent,
      },
      {
        path:'sundary_debtor',
        component:SundryDebtorComponent,
      },
      
    ],
    pathMatch: 'prefix',
    data: {
      routerLinkActiveOptions: { exact: true, pathMatch: 'full' }
    }
  },
]

@NgModule({
  declarations: [],
  imports: [RouterModule.forChild(routes), CommonModule],
  exports: [RouterModule]
})
export class SaleRoutingModule { }
